import React from "react";

import "./learn.css";

const LearningOptions = (props) => {
  const options = [
    { text: "u i", handler: props.actionProvider.handlePackages, id: 1 },
    { text: "Dch V", handler: props.actionProvider.handleServices, id: 2 },
    { text: "Covid-19", handler: props.actionProvider.handleCovid, id: 3 },
    { text: "An Ton", handler: props.actionProvider.handleSafe, id: 4 },
    { text: "V chng ti", handler: props.actionProvider.handleAbout, id: 5 },
  ];

  const optionsMarkup = options.map((option) => (
    <button
      className="learning-option-button"
      key={option.id}
      onClick={option.handler}
    >
      {option.text}
    </button>
  ));

  return <div className="learning-options-container">{optionsMarkup}</div>;
};

export default LearningOptions;
