class ActionProvider {
  constructor(createChatBotMessage, setStateFunc) {
    this.createChatBotMessage = createChatBotMessage;
    this.setState = setStateFunc;
  }
  greet = () => {
    const message = this.createChatBotMessage(
      "Hi cho bn, Ti l chatbot. Ti c th gii p cho cc cu hi thng gp"
    );

    this.updateChatbotState(message);
  };
  text = () => {
    const message = this.createChatBotMessage("Ti c th gip g cho bn?", {
      widget: "learningOptions",
    });

    this.updateChatbotState(message);
  };
  keyboard = () => {
    const message = this.createChatBotMessage("Xin li, Ti khng c tay, Vy nn ti khng chi bn phm c :(", {
      widget: "learningOptions",
    });

    this.updateChatbotState(message);
  };
  handlePackages = () => {
    const message = this.createChatBotMessage(
      "Bn mun tm hiu thm v u i?",
      {
        widget: "packagesLinks",
      }
    );

    this.updateChatbotState(message);
  };
  handleServices = () => {
    const message = this.createChatBotMessage(
      "Bn mun tm hiu thm v Dch v?",
      {
        widget: "servicesLinks",
      }
    );

    this.updateChatbotState(message);
  };
  handleCovid = () => {
    const message = this.createChatBotMessage(
      "Bn mun tm hiu thm v Du lch trong dch Covid?",
      {
        widget: "covidLinks",
      }
    );

    this.updateChatbotState(message);
  };
  handleSafe = () => {
    const message = this.createChatBotMessage(
      "Bn mun tm hiu thm v chnh sch An ton ca Stravel ?",
      {
        widget: "safeLinks",
      }
    );

    this.updateChatbotState(message);
  };
  handleAbout = () => {
    const message = this.createChatBotMessage(
      "Bn mun tm hiu thm v Stravel ?",
      {
        widget: "aboutLinks",
      }
    );

    this.updateChatbotState(message);
  };

  updateChatbotState(message) {
    this.setState((prevState) => ({
      ...prevState,
      messages: [...prevState.messages, message],
    }));
  }
}

export default ActionProvider;
