import Header from "../../components/sydney/Header2";
import Footer from "../../components/sydney/Footer2";
import { Link } from "react-router-dom";
import React from "react";
import { useEffect } from "react";
import "../../Style/citys/night.css";
import "../../Style/citys/style.css";
class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      home: [],
    };
  }
  componentDidMount() {
    fetch("https://apiforstravel.herokuapp.com/pages/5")
      .then((res) => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            home: result.home,
          });
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error,
          });
        }
      );
  }
  render() {
    return (
      <section className="home" id="home">
        <div className="content">
          <span data-aos="fade-up" data-aos-delay="100">
            {this.state.home.name}
          </span>
          <h3 data-aos="fade-up" data-aos-delay="200">
            {this.state.home.content}
          </h3>
          <p data-aos="fade-up" data-aos-delay="300">
            {this.state.home.textcontent}
          </p>
          <a
            data-aos="fade-up"
            data-aos-delay="450"
            href="#destination"
            className="btn"
          >
            Tm hiu thm
          </a>
        </div>
      </section>
    );
  }
}

const Book = () => {
  return (
    <section className="book-form" id="book-form">
      <form action="">
        <div data-aos="zoom-in" data-aos-delay="50" className="inputBox">
          <span>
            <h1>chuyn i c u i tt nht</h1>
          </span>
        </div>
        <div data-aos="zoom-in" data-aos-delay="100" className="inputBox">
          <span>S in thoi</span>
          <input type="text" placeholder="0912345678" maxLength={11} />
        </div>
        <div data-aos="zoom-in" data-aos-delay="150" className="inputBox">
          <span>Email</span>
          <input type="email" placeholder="email@gmail.com" />
        </div>
        <div data-aos="zoom-in" data-aos-delay="200" className="inputBox">
          <span>Thi gian i</span>
          <input type="datetime-local" />
        </div>
        <div data-aos="zoom-in" data-aos-delay="250" className="inputBox">
          <span>Ti c</span>
          <input type="number" min={2} max={8} placeholder="T 2-8 ngi" />
        </div>
        <div data-aos="zoom-in" data-aos-delay="300" className="inputBox">
          <span>Ti thanh ton bng</span>
          <select>
            <option value="momo">Momo</option>
            <option value="bank">Chuyn khon</option>
            <option value="card">Th VISA</option>
            <option value="none">Thanh ton khi n ni</option>
          </select>
        </div>
        <Link
          data-aos="zoom-in"
          data-aos-delay="350"
          className="btn"
          to="/success"
        >
          t Ngay
        </Link>
      </form>
    </section>
  );
};

export default function SydneyBook() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <div className="Pages Sydney">
      <Header />
      <div className="container">
        <Home />
        <Book />
      </div>
      <Footer />
    </div>
  );
}
